:root {
  --global-color-bright-theme: white;
  --global-color-dark-theme: black;
  --global-text-bright-theme: black;
  --global-text-dark-theme: white;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-display: optional;
  /* background-color: var(--global-color-bright); */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.noteList {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.card {
  display: inline-flex;
  flex-direction: row;
  margin: 0.5rem;
}

.btn_wrapper {
  display: flex;
  justify-content: space-between;
}

.center {
  display: flex;
  justify-content: center;
}
